import './_mine-field.scss';

import { MinesweeperContext } from 'contexts/minesweeper';
import React, { SyntheticEvent, useCallback, useContext, useMemo } from 'react';
import { IndexUtils } from 'utils/index-utils';
import { CellEvent, CellVM } from 'view-models';

import CellFactory from '../CellFactory';

const CELL_SIZE = 32;

const MineField = () => {
  const {
    mineFieldProps: { rowsCount, columnsCount },
    mineField,
    mineFieldStatus: { isSetup, isBusted, isSolved },
    setupMineField,
    revealCell,
    toggleFlag,
    revealNeighbors,
  } = useContext(MinesweeperContext);

  const indexUtils = useMemo(() => new IndexUtils(rowsCount), [rowsCount]);

  const classNamePrefix = 'MineField';
  const rowClassName = `${classNamePrefix}Row`;

  const handleTableContextMenu = (event: SyntheticEvent<HTMLTableElement>) => {
    event.preventDefault();
  };

  const handleCellClick = useCallback(
    (cell: CellVM) => {
      (isSetup ? revealCell : setupMineField)(cell);
    },
    [isSetup, revealCell, setupMineField]
  );

  const handleMouseDown = useCallback(
    ({ target, nativeEvent }: CellEvent, cell: CellVM) => {
      const handleMouseUp = (event: Event) => {
        if ((nativeEvent as MouseEvent).which !== (event as MouseEvent).which) {
          revealNeighbors(cell);
        }

        target.removeEventListener('mouseup', handleMouseUp);
      };

      target.addEventListener('mouseup', handleMouseUp);
    },
    [revealNeighbors]
  );

  const handleCellContextMenu = useCallback(
    (cell: CellVM) => {
      toggleFlag(cell);
    },
    [toggleFlag]
  );

  return (
    <table
      className={classNamePrefix}
      style={{ width: rowsCount * CELL_SIZE }}
      onContextMenu={handleTableContextMenu}
    >
      <tbody>
        <tr
          className={
            isBusted || isSolved
              ? `${rowClassName} ${rowClassName}__disabled`
              : void 0
          }
        >
          {mineField.map((cell, cellIndex) => {
            const { rowIndex, columnIndex } = indexUtils.to2D(cellIndex);

            return (
              <CellFactory
                vm={cell}
                withBorderRight={rowIndex === rowsCount - 1}
                withBorderBottom={columnIndex === columnsCount - 1}
                onClick={handleCellClick}
                onMouseDown={handleMouseDown}
                onContextMenu={handleCellContextMenu}
                key={cell.uuid}
              />
            );
          })}
        </tr>
      </tbody>
    </table>
  );
};

export default MineField;
